class Integral extends React.Component {
    constructor(props) {
        super(props);
        this.state = {Substage: 0};

        this.incrementSubstage = this.incrementSubstage.bind(this);
    }

    incrementSubstage() {
        console.log("test")
        this.setState({Substage: this.state.Substage + 1});
    }

    render() {
        switch(this.state.Substage) {
            case 0:
                return (
                    <div class="center">
                        <h1><u>Integral Test</u></h1><br/>
                        <p><TexInline Formula="Let f(n)" /> such that <TexInline Formula="f(n)=a_{n}"/></p>
                        <p>Is <TexInline Formula="f(n)"/> continuous, decreasing, and positive on the same interval as your sum?</p>
                        <br/><button onClick={this.incrementSubstage}>Yes</button>
                        <button onClick={this.props.inc}>No</button>
                    </div>
                );
            case 1:
                return (
                    <div class="center">
                        <h1><u>Integral Test</u></h1><br/>
                        <p>Could you actually evaluate this integral?</p>
                        <TexBlock Formula="\int_{a}^{\infty} f(n)dn"/>
                        <br/><button onClick={this.incrementSubstage}>Yes</button>
                        <button onClick={this.props.inc}>No</button>
                    </div>
                );
            case 2:
                return (
                    <div class="center">
                        <h1><u>Integral Test</u></h1><br/>
                        <p>Ok, so do that. Did the integral converge?</p>
                        <br/><button onClick={this.props.conv}>Yes</button>
                        <button onClick={this.props.div}>No</button>
                    </div>
                )
        }
    }
}

class Ratio extends React.Component {
    constructor(props) {
        super(props);
        this.state = {Substage: 0, Alt: false};

        this.incrementSubstage = this.incrementSubstage.bind(this);
        this.setAlt = this.setAlt.bind(this);
    }

    incrementSubstage() {
        console.log("test")
        this.setState({Substage: this.state.Substage + 1});
    }

    setAlt() {
        console.log("alt");
        this.setState({Alt: true});
    }

    render() {
        if(this.state.Alt) return (
            <div class="center">
                <h1>Ratio Test</h1>
                <p>I suppose I can't make you evaluate that, but what does this accomplish? I mean, I've put a lot of time into this assignment and you're just going to press
                    a button that says "no" just for the sake of it? You know you have to restart from the beginning now, right? You know that because of this, I have to <i>make</i>
                    that reset button, right? You're just making more work for me. Why would you do that? And now that I've typed out this block of text, I've lost even more time.
                    Because of you. Thanks.
                </p>
            </div>
        )
        switch(this.state.Substage) {
            case 0:
                return (
                    <div class="center">
                        <h1><u>Ratio Test</u></h1><br/>
                        <p>Does your series look like a geometric series, but isn't actually a geometric series?</p>
                        <p>or</p>
                        <p>Does your series contain factorials or exponentials?</p>
                        <br/><button onClick={this.incrementSubstage}>Yes</button>
                        <button onClick={this.props.inc}>No</button>
                    </div>
                )
            case 1:
                return (
                    <div class="center">
                        <h1><u>Ratio Test</u></h1><br/>
                        <p>Would you be so kind as to evaluate the following limit and state the result?</p>
                        <TexBlock Formula="\lim_{n\to\infty} \Big|\frac{a_{n+1}}{a_{n}}\Big| = L"/>
                        <br/><button onClick={this.props.conv}>{"L < 1"}</button>
                        <button onClick={this.props.div}>{"L > 1"}</button>
                        <button onClick={this.props.inc}>{"L = 1"}</button>
                        <button onClick={this.setAlt}>No</button>
                    </div>
                )
        }
    }
}

class Comparison extends React.Component {
    constructor(props) {
        super(props);
        this.state = {Substage: 0};

        this.incrementSubstage = this.incrementSubstage.bind(this);
    }

    incrementSubstage(n) {
        console.log("test")
        this.setState({Substage: this.state.Substage + n});
    }

    render() {
        switch(this.state.Substage) {
            case 0:
                return (
                    <div class="center">
                        <h1><u>Comparison</u></h1><br/>
                        <p>All that's left to do is try comparison.</p>
                        <p>Would you like to try limit comparison or direct comparison?</p>
                        <button onClick={() => this.incrementSubstage(1)}>Limit comparison</button>
                        <button onClick={() => this.incrementSubstage(2)}>Direct comparison</button>
                        <button onClick={() => this.incrementSubstage(3)}>No</button>
                    </div>
                )
            case 1:
                return (
                    <div class="center">
                        <h1><u>Limit Comparison</u></h1><br/>
                        <p>Try to think of another series <TexInline Formula="b_{n}"/> such that</p>
                        <TexBlock Formula="\lim_{n\to\infty} \Big|\frac{a_{n}}{b_{n}}\Big| \in\mathbb{R}^{+}"/>
                        <p>or</p>
                        <TexBlock Formula="\lim_{n\to\infty} \Big|\frac{b_{n}}{a_{n}}\Big| \in\mathbb{R}^{+}"/>
                        <p>Did the series you compared to diverge or converge?</p>
                        <button onClick={this.props.conv}>Converge</button>
                        <button onClick={this.props.div}>Diverge</button>
                        <button onClick={() => this.incrementSubstage(2)}>No</button>
                    </div>
                )
            case 2:
                return (
                    <div class="center">
                        <h1><u>Direct Comparison</u></h1><br/>
                        <p>Try to think of another series <TexInline Formula="b_{n}"/> such that</p>
                        <p><TexInline Formula="\sum_{n=1}^{\infty}b_{n}\in\mathbb{R}"/> and <TexInline Formula="0\leq a_{n}\leq b_{n}"/></p>
                        <p>or</p>
                        <p><TexInline Formula="\sum_{n=1}^{\infty}b_{n}\notin\mathbb{R}"/> and <TexInline Formula="0\leq b_{n}\leq a_{n}"/></p>
                        <p>Did the series you compared to diverge or converge?</p>
                        <button onClick={this.props.conv}>Converge</button>
                        <button onClick={this.props.div}>Diverge</button>
                        <button onClick={() => this.incrementSubstage(1)}>No</button>
                    </div>
                )
            case 3:
                return (
                    <div class="center">
                        <h1><u>seriously</u></h1><br/>
                        <p>you got this far and you're just going to say no?</p>
                    </div>
                )
        }
    }
}

class SeriesBlock extends React.Component {
    render() {
        return (
            <TexBlock Formula={"\\sum_{n=1}^{\\infty}" + this.props.Summand} />
        );
    }
}

class TexBlock extends React.Component {
    render() {
        return (
            <MathJax.Provider>
                    <MathJax.Node formula={this.props.Formula} />
            </MathJax.Provider>
        )
    }
}

class TexInline extends React.Component {
    render() {
        return (
            <MathJax.Provider>
                    <MathJax.Node inline formula={this.props.Formula} />
            </MathJax.Provider>
        )
    }
}

ReactDOM.render(<MainWindow/>, document.getElementById('root'));
